*{
      margin: 0;
      padding: 0;
      box-sizing: border-box;
}

.smallCard{
      border: none;
      border-radius: 5px ;
      height: 12rem;
      color: rgb(0, 0, 0);
      border: 3px solid #6495ED; 
      text-decoration: none;
      width: 31.2%;
      margin: 2rem 10px 1rem 10px;
     background-color: rgb(255, 255, 255);
      cursor: pointer;
      transition: all 0.3s;
      
}
.cases{
      color: rgb(92, 90, 90);
      font-weight: 300;
}

.numCases{
      color: #6495ED; 
      text-align: center;
      margin-top: 10px;
      letter-spacing: 1px;
      font-size: 35px;
      font-weight: 900;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
#india p{
      color: tomato;
      font-weight: bold;
}




.smallCard h2{
      text-align: center;
      margin: 10px 0 15px 0;
      font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.smallCard:hover{
      border: none;
      border-radius: 5px ;
      height: 12rem;
      width: 31.2%;
      border: 3px solid #6495ED; 
      margin: 10px;
      box-shadow: 1px 2px 3px rgb(170, 169, 169);
      transition: ease-in-out .2s;
      background-color: rgb(255, 255, 255);
      cursor: pointer;
}



.img img{
      width: 70px;
      height: 25px;
     
}

.img{
      text-align: right;
      width: 100%;
      height: 10px;
      margin:0;
}

#india{
      color: white;
      text-align: center;
      margin-top: 20px;
      font-size: 18px;
      font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
      letter-spacing: 3px;
      width: 100%;
}

.cases{
      font-size: 20px;
      text-align: center;
      font-weight: 700;
}

@media (max-width:500px){

      .smallCard{
           
            border: none;
            border-radius: 5px ;
            height: 12rem;
            color: rgb(0, 0, 0);
            text-decoration: none;
            width:100%;
            border: 3px solid #6495ED;              
            margin: 2rem 10px 1rem 10px;
           background-color: rgb(255, 255, 255);
            cursor: pointer;
      }

      #covid{
            font-weight: 900;
            color: rgb(255, 255, 255);
            margin-top: 2rem ;
            font-size: 16px;
          }
      .smallCard:hover{
            width: 100%;
      }

      #updatedTime{

            position: absolute;
            right: 0;
            z-index: 999;
            font-size: 12px;
            top: 0;
            margin: 0 25px 10px 10px;
            font-weight: bold;
            color: white;
          }

      .numCases{
            color: #6495ED; 
            text-align: center;
            margin-top: 10px;
            font-size: 25px;
            font-weight: bold;
      }
      
      
}

@media (min-width:500px) and (max-width:800px){

      .smallCard{
           
            border: none;
            border-radius: 5px ;
            height: 10rem;
            color: rgb(0, 0, 0);
            text-decoration: none;
            width:41%;
            margin: 2rem 10px 1rem 10px;
            border: 3px solid #6495ED;  
           background-color: white;
            cursor: pointer;
      }

      
      .smallCard:hover{
            width: 41vw;
      }
      .numCases{
            color: #6495ED; 
            text-align: center;
            margin-top: 10px;
            font-size: 23px;
            font-weight: 900;
            
      }
      
}

@media (min-width:800px) and (max-width:1274px){

      .smallCard{
           
            border: none;
            border-radius: 5px ;
            height: 12rem;
            color: rgb(0, 0, 0);
            text-decoration: none;
            width:35%;
            margin: 3rem 10px 1rem 10px;
            border: 3px solid #6495ED;  
           background-color: white;
            cursor: pointer;
      }
      

      .smallCard:hover{
            width:35%;


      }


}