* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbarContainer {
  background-color: #6495ED;
  width: 100vw;
  height: 12em;
  position: absolute;
  display: flex;
  justify-content: start;
}

.search {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 30px 0 10px;
}

.info {
  width: 10vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 1em;
}

.info .fa-bell,
#navuser,
.fa-sign-out-alt {
  font-size: 1.2em;
  color: whitesmoke;
  cursor: pointer;
  padding: 0.3em;
}
.info .fas:hover,
.far:hover,
#navuser:hover {
  font-size: 21px;
}

#profile img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

#searchIcon {
  position: absolute;
  margin-left: 10px;
  color: rgb(160, 156, 156);
}

#search {
  width: 40%;
  height: 35px;
  border-radius: 50px;
  border: 0.3px solid rgb(233, 233, 233);
  outline: none;
  background-color: #ffffff;
  font-size: 16px;
  padding: 0 5px 0 2rem;
  color: rgb(160, 156, 156);
}

#logoimg img {
  width: 30px;
  height: 30px;
}
#logoimg {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px 0 5px;
}
.logoImg h3 {
  display: flex;
  width: 10em;
  letter-spacing: 3px;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5em;
}

#link {
  text-decoration: none;
}

.logoImg {
  display: flex;
  text-decoration: none;
  height: 35%;
}

.fa-align-left {
  font-size: 23px;
  color: white;
  margin-left: 5rem;
  cursor: pointer;
}

.fa-align-left:hover {
  font-size: 25px;
  color: white;
  margin-left: 5rem;
  cursor: pointer;
}

@media (max-width: 500px) {
  .navbarContainer {
    background-color: #6495ED;
    width: 100vw;
    height: 12em;
    position: absolute;
    display: flex;
    justify-content: start;
  }

  #search {
    display: none;
  }

  #searchIcon {
    position: relative;
  }

  .logoImg h3 {
    display: flex;
    width: 10em;
    letter-spacing: 3px;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 0.8em;
  }

  .fa-search{
    color: white;
  }

  #logoimg img {
    width: 1.5em;
    height: 1.5em;
  }
}
