* {
  margin: 0;
  padding: 0;
}
.sidebar {
  background-color: #ffffff;
  box-shadow: 0 2px 3px rgb(245, 244, 244);
  height: 100vh;
  width: 25%;
  position: sticky;
  top: 0;
  border-right: 0.3px inset #b6b8bb;
  border-top-right-radius: 2px;
  overflow-y: scroll;
  transition: ease-in 0.3s;
}



.sidebar-container{
  width:100%;
}
.sidebar.active {
  background-color: #ffffff;
  box-shadow: 0 2px 10px rgb(133, 131, 131);
  height: 100vh;
  width: 5vw;
  position: sticky;
  top: 0;
  border-top-right-radius: 2px;
  overflow-y: scroll;
  transition: ease-in 0.3s;
}



.hideshow_sidebar {
  width: 10%;
  font-size: 20px;
color: white;
  margin-left: 14rem;
  cursor: pointer;
  position: absolute;
  transition: ease-in 0.3s;
}
.hideshow_sidebar.active {
  width: 10%;
  font-size: 20px;
  margin-left: 2.2rem;
  color: white;

  cursor: pointer;

  transition: ease-in 0.2s;
  transform: rotate(180deg);
}



.covidIcon.active{
  display: none;
  width: 10%;
}


.pages.active{
  width:85%;
  background-color: #6495ED;
  color: rgb(255, 255, 255); 
  transition: ease .5s;
  font-size: 20px;
}
.profile.active{
  width:85%;
  background-color: #6495ED;
  color: rgb(255, 255, 255);  
  transition: ease .5s;
  font-size: 20px;

}

.hidetoggle{
  background-color: #6495ED;
  box-shadow: 1px 2px 5px  rgb(114, 113, 113);
  height: 30px;
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 999;
  top: 0;
}





.PagesIcon.active{
  display: none;
  width: 10%;
}
.ProfileIcon.active{
  display: none;
  width: 10%;
}
.homeIcon.active {
  width: 10%;
  display: none;
}

.Covid19.active{
  width: 85%;
  background-color: #6495ED;
  color: rgb(255, 255, 255); 
  transition: ease .5s;
  font-size: 20px;

}



.dashboard.active {
  width: 85%;
 background-color: #6495ED;
 color: rgb(255, 255, 255);  
 transition: ease .5s;
 font-size: 20px;

}
.expand_dash,.expand_covid,.expand_pages{
  transform: rotate(-90deg);
  transition: ease-in-out .2s;

}
.expand_dash.active,.expand_covid.active,.expand_pages.active{
  transform: rotate(0deg);
  transition: ease-in-out .2s;
}


.bi-house-door {
  font-size: 20px;
 
}





.h4.active {
  display: none;
  transition: ease 0.5s;
}


::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}

h4 {
  color: #6495ED;
  padding: 20px;
}

.dashboard,
.pages,
.Covid19,
.profile {
  background-color: #6495ED; 
  width: 95%;
  font-size: 1em;
  margin: 20px 0 20px 5px;
  padding: 20px;
  height: 50px;
  color: white;
  display: flex;
  justify-content: center;
  border-radius: 3px;
  align-items: center;
  font-weight: 600;
  letter-spacing: 2px;
  box-shadow: 0 2px 2px gray;
  cursor: pointer;
  transition: ease 0.5s;
}




.homeIcon,.covidIcon,.PagesIcon,.ProfileIcon{

  width:100%;
  display: flex;
  justify-content: space-between;
  margin: 0 .5em 0 0.5em ;
}

.dashboardList,
.covidList,
.Pages {
  text-decoration: none;
  list-style-type: none;
  display: none;
  flex-direction: column;
  line-height: 40px;
  margin: 20px 0;
}


.covidList.active {
  display: block;
}

.sidelink{
text-decoration: none;
color: #6495ED;
font-size: 1.1em;

}


.sidelink.active{
  font-size: 18px;
  font-weight: 600;
}




li{
  width:80%;
  margin-left: 1em;
  font-weight: 600;

}

.right.active{
  display: none;
}

 


.sidebarFooter {
  width: 100%;
  height: 30px;
}

