*{
      margin: 0;
      padding: 0;
      box-sizing: border-box;
}

.home_Container{
      display: flex;
      flex-wrap: wrap;
      width: 100%;
}

.top{
      display: flex;
      justify-content: space-evenly;
      width: 100%;

}
#covid{
  font-weight: 900;
  color: rgb(255, 255, 255);
}

#updatedTime{

  position: absolute;
  right: 0;
  z-index: 999;
  top: 0;
  margin: 0 25px 10px 10px;
  font-weight: bold;
  color: white;
}

.safty{
      display: flex;
      width: 98%;
      margin: 1em auto;
      border: 1px solid white;
}

.safty img{
      width: 100%;
      height:100%;
}

.smallCardContainer{
      width: 100%;
}
.middleCards{
      width: 100%;
      display: flex;
}

.firstContainer{
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;

      flex-wrap: wrap;
}

.smallCardsHolder, .thirdCardsHolder{
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;

}
.loader,
.loader:before,
.loader:after {
  background: #44a1f8;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #44a1f8;
  text-indent: -9999em;
  position: fixed;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


    
#loader{
      z-index: 9999;
      width: 75%;
      height: 70vh;
      position: relative;
      font-weight: bold;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      /* background-color: rgb(248, 200, 200); */
}
